import { Link } from 'react-router-dom';
import * as C from './styled';

export const Header = () => {
    return (
        <C.Container>
            <C.Logo>Junior Rodrigues</C.Logo>
            <C.MenuBar>
                <Link to="/">Home</Link>
                <Link to="/projects">Projects</Link>
                <Link to="/contact">Contact</Link>
            </C.MenuBar>
        </C.Container>
    );
}