import styled from 'styled-components';

export const Container = styled.footer`
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    color: #fff;
    padding: 5px;
    font-size: 35px;
    line-height: 35px;
    justify-content: center;
    align-items: center;
    background: linear-gradient(150deg, var(--vermelho), var(--vermelho) 20%, var(--preto) 25%, var(--preto), var(--preto) 75%, var(--vermelho) 90%, var(--vermelho));
    font-family: var(--tangerine);
`;