import styled from 'styled-components';

export const Container = styled.header`
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    padding: 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
    background: linear-gradient(150deg, var(--vermelho), var(--vermelho) 15%, var(--preto) 40%, var(--preto), var(--preto) 65%, var(--vermelho) 90%, var(--vermelho));
`;

export const Logo = styled.div`
    color: var(--branco);
    font-family: var(--tangerine);
    flex: 1;
    font-size: 45px;
    line-height: 25px;
    padding: 10px;
`;

export const MenuBar = styled.nav`
    line-height: 25px;
    flex: 1;
    display: flex;
    justify-content: flex-end;
    padding: 10px;

    a {
        font-family: var(--tangerine);
        font-size: 40px;
        text-decoration: none;
        display: inline-block;
        padding: 0 10px;
        color: var(--branco);
        &:hover {
            color: var(--branco);
        }
    }
`;